import React from "react";
import { push } from "connected-react-router";
import {
  Card,
  Button,
  CardMedia,
  Typography,
  CardContent,
  CardActions,
  CardActionArea,
} from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import { store } from "../../../_store/store.config";
import { ILesson } from "../../../_types/lesson.types";
import { thumbnailsFinder } from "../../../_helpers/constants";
import { addFavorites } from "../../../_actions/profile.actions";
import { DefaultProfileState } from "../../../_types/profile.types";
import { CardFavorite } from "../../Layout/Buttons/CardFavorite";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: 300,
      maxHeight: 360,
      position: "relative",
      margin: theme.spacing(2),
      [theme.breakpoints.down(350)]: {
        margin: theme.spacing(1),
      },
    },
    cardAction: {
      justifyContent: "space-between",
      margin: "inherit",
    },
    content: {
      minHeight: 150,
    },
    rightChip: {
      backgroundColor: theme.palette.background.default,
      position: "absolute",
      zIndex: 30,
      right: theme.spacing(1),
      top: theme.spacing(1),
    },
    svgSize: {
      color: theme.palette.secondary.main,
      width: 34,
      height: 34,
      "&:hover": {
        color: theme.palette.primary.main,
      },
    },
    loading: {
      maxWidth: 34,
      maxHeight: 34,
      color: theme.palette.secondary.main,
    },
  })
);

type Props = {
  lesson: ILesson;
  user: DefaultProfileState;
  type: string;
};

export const LessonCard: React.FC<Props> = ({ lesson, user, type }) => {
  const classes = useStyles();
  const { title, featured_img, excerpt, author, id } = lesson;
  const { loading, favorite } = user;

  const clickHandler = () => {
    store.dispatch(push(`/${type}/${id}`));
  };

  const handleFavorite = () => {
    store.dispatch(
      addFavorites({ title, featured_img, excerpt, author, id, type })
    );
  };

  return (
    <Card className={classes.root}>
      <CardFavorite
        isFavorite={favorite?.some((fav) => fav.id === id)}
        loading={loading}
        handleFavorite={handleFavorite}
      />
      <CardActionArea onClick={clickHandler}>
        <CardMedia
          component="img"
          alt={title}
          height="140"
          image={thumbnailsFinder(featured_img)}
          title={title}
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="h5" component="h2">
            {title}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {excerpt}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions className={classes.cardAction}>
        <Typography variant="body2" color="textSecondary" component="p">
          از {author}
        </Typography>
        <Button size="medium" color="secondary" onClick={clickHandler}>
          ادامه درس
        </Button>
      </CardActions>
    </Card>
  );
};
