import React from "react";
import { push } from "connected-react-router";
import {
  Card,
  Button,
  CardMedia,
  Typography,
  CardContent,
  CardActions,
  CardActionArea,
} from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import { addFavorites } from "../../../_actions/profile.actions";
import { CardFavorite } from "../../Layout/Buttons/CardFavorite";
import { thumbnailsFinder } from "../../../_helpers/constants";
import { IFavorite } from "../../../_types/profile.types";
import { store } from "../../../_store/store.config";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: 300,
      maxHeight: 360,
      position: "relative",
      margin: theme.spacing(2),
      [theme.breakpoints.down(350)]: {
        margin: theme.spacing(1),
      },
    },
    cardAction: {
      justifyContent: "space-between",
      margin: "inherit",
    },
    content: {
      minHeight: 150,
    },
  })
);

type Props = {
  favorite: IFavorite;
};

export const FavoriteCard: React.FC<Props> = ({ favorite }) => {
  const classes = useStyles();
  const { title, featured_img, excerpt, author, id, type } = favorite;

  const clickHandler = () => {
    store.dispatch(push(`/${type}/${id}`));
  };
  const handleFavorite = () => {
    store.dispatch(addFavorites({ ...favorite, type: "lesson" }));
  };

  return (
    <Card className={classes.root}>
      <CardFavorite
        isFavorite={true}
        loading={false}
        handleFavorite={handleFavorite}
      />
      <CardActionArea onClick={clickHandler}>
        <CardMedia
          component="img"
          alt={title}
          height="140"
          image={thumbnailsFinder(featured_img)}
          title={title}
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="h5" component="h2">
            {title}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {excerpt}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions className={classes.cardAction}>
        <Typography variant="body2" color="textSecondary" component="p">
          از {author}
        </Typography>
        <Button size="medium" color="secondary" onClick={clickHandler}>
          ادامه درس
        </Button>
      </CardActions>
    </Card>
  );
};
