import React, { useEffect, useState } from "react";
import {
  CircularProgress,
  createStyles,
  IconButton,
  makeStyles,
  Theme,
} from "@material-ui/core";
import { Favorite, FavoriteBorder } from "@material-ui/icons";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    rightChip: {
      backgroundColor: theme.palette.background.default,
      position: "absolute",
      zIndex: 30,
      right: theme.spacing(1),
      top: theme.spacing(1),
    },
    svgSize: {
      color: theme.palette.secondary.main,
      width: 34,
      height: 34,
      "&:hover": {
        color: theme.palette.primary.main,
      },
    },
    loading: {
      maxWidth: 34,
      maxHeight: 34,
      color: theme.palette.secondary.main,
    },
  })
);

interface CardFavoriteProps {
  isFavorite?: boolean;
  loading: boolean;
  handleFavorite: () => void;
}

export const CardFavorite: React.FC<CardFavoriteProps> = ({
  isFavorite,
  loading,
  handleFavorite,
}) => {
  const classes = useStyles();
  const [changed, setChanged] = useState(loading);
  useEffect(() => {
    if (changed) setChanged(loading);
    return () => {
      setChanged(false);
    };
  }, [loading, changed]);
  const changeFavorite = () => {
    setChanged(true);
    handleFavorite();
  };
  return (
    <IconButton
      aria-label="add to favorites"
      className={classes.rightChip}
      onClick={changeFavorite}
    >
      {changed ? (
        <CircularProgress className={classes.loading} />
      ) : isFavorite ? (
        <Favorite className={classes.svgSize} />
      ) : (
        <FavoriteBorder className={classes.svgSize} />
      )}
    </IconButton>
  );
};
