import { CallHistoryMethodAction, RouterState } from "connected-react-router";

import { DefaultUserState, UserActionTypes } from "./auth.types";
import { SnackActionTypes, DefaultSnackState } from "./snackbar.types";
import { ProfileActionTypes, DefaultProfileState } from "./profile.types";
import {
  PostActionTypes,
  DefaultPostsState,
  DefaultPostState,
} from "./post.types";
import {
  GuideActionTypes,
  DefaultGuidesState,
  DefaultGuideState,
} from "./guide.types";
import {
  CourseActionTypes,
  DefaultCoursesState,
  DefaultCourseState,
} from "./course.types";
import {
  LessonActionTypes,
  DefaultLessonState,
  DefaultLessonsState,
} from "./lesson.types";
import {
  ExerciseActionTypes,
  DefaultExercisesState,
  DefaultExerciseState,
} from "./exercise.types";
import { IMelodition, PlayerActionTypes } from "./player.types";

export type AppActions =
  | CallHistoryMethodAction
  | PostActionTypes
  | PlayerActionTypes
  | LessonActionTypes
  | ExerciseActionTypes
  | CourseActionTypes
  | GuideActionTypes
  | SnackActionTypes
  | UserActionTypes
  | ProfileActionTypes;

export type BankType = {
  posts: DefaultPostsState;
  guides: DefaultGuidesState;
  courses: DefaultCoursesState;
  lessons: DefaultLessonsState;
  exercises: DefaultExercisesState;
};
export type SingleType = {
  post: DefaultPostState;
  guide: DefaultGuideState;
  course: DefaultCourseState;
  lesson: DefaultLessonState;
  exercise: DefaultExerciseState;
};

export type AppState = {
  router: RouterState;
  single: SingleType;
  bank: BankType;
  user: DefaultProfileState;
  melodition: IMelodition;
  snackbar: DefaultSnackState;
  auth: DefaultUserState;
};

export const BankPages = {
  posts: { name: "POSTS", api: "/post/v2" },
  guides: { name: "GUIDES", api: "/lesson/v2/guide" },
  courses: { name: "COURSES", api: "/lesson/v2/course" },
  lessons: { name: "LESSONS", api: "/lesson/v2?course=123" },
  exercises: { name: "EXERCISES", api: "/lesson/v2?course=125" },
};
